















import { catalogApiRef } from '@backstage/plugin-catalog-react';
import { Button } from '@material-ui/core';
import React from 'react';
import useAsync from 'react-use/lib/useAsync';
import { DomainCard } from '../DomainCard';

import {
  Content,
  ContentHeader,
  EmptyState,
  ItemCardGrid,
  Progress,
  SupportButton,
  WarningPanel,
} from '@backstage/core-components';

import { useApi } from '@backstage/core-plugin-api';

const Body = () => {
  const catalogApi = useApi(catalogApiRef);
  const {
    value: entities,
    loading,
    error,
  } = useAsync(async () => {
    const response = await catalogApi.getEntities({
      filter: { kind: 'domain' },
    });
    return response.items ;
  }, [catalogApi]);

  if (loading) {
    return React.createElement(Progress, null );
  }

  if (error) {
    return (
      React.createElement(WarningPanel, { severity: "error", title: "Could not load domains."   ,}
        , error.message
      )
    );
  }

  if (!entities?.length) {
    return (
      React.createElement(EmptyState, {
        missing: "info",
        title: "No domains to display"   ,
        description: "You haven't added any domains yet."     ,
        action: 
          React.createElement(Button, {
            variant: "contained",
            color: "primary",
            href: "https://backstage.io/docs/features/software-catalog/descriptor-format#kind-domain",}
          , "Read more"

          )
        ,}
      )
    );
  }

  return (
    React.createElement(ItemCardGrid, null
      , entities.map((entity, index) => (
        React.createElement(DomainCard, { key: index, entity: entity,} )
      ))
    )
  );
};





export const DomainExplorerContent = ({
  title,
}) => {
  return (
    React.createElement(Content, { noPadding: true,}
      , React.createElement(ContentHeader, { title: title ?? 'Domains',}
        , React.createElement(SupportButton, null, "Discover the domains in your ecosystem."     )
      )
      , React.createElement(Body, null )
    )
  );
};
